import React, { Component } from 'react';
import { Table, Button, Modal, Form, Input, Radio } from 'antd';
import { observer } from 'mobx-react';
import BasicLayout from '../../layout/index';
const { TextArea } = Input;
import styles from  '../../less/index.config.less';
import modalStyle from '../../less/modal_common.less';
import multipleClass from '../../helpers/multiple_class';
import store from '../../stores/service.config';
class AddService extends Component{
	render(){
		let { visible } = this.props;
		return (
			<Modal onCancel={this.closeModal} width={800} visible={visible} title={<p className={multipleClass(modalStyle, 'modal-title')}><small></small></p>}>
				<Form>
					<Form.Item label='' labelCol={{span: 6}} wrapperCol={{span: 14}}>
						<Radio.Group>
							<Radio value='1'></Radio>
							<Radio value='2'></Radio>
						</Radio.Group>
					</Form.Item>
					<Form.Item label='' labelCol={{span: 6}} wrapperCol={{span: 12}}>
						<Input placeholder=''/>
					</Form.Item>
					<Form.Item label='' labelCol={{span: 6}} wrapperCol={{span: 14}}>
						<TextArea autosize={{minRows: 5}} placeholder='/'/>
					</Form.Item>
				</Form>
			</Modal>
		)
	}
	closeModal = () => {
		store.visible = false;
	}
}
const AddModalWithForm = Form.create()(AddService);
@observer
class ServiceConfig extends Component{
	render(){
		let { visible } = store;
		return (
			<BasicLayout title=''>
				<div style={{textAlign: 'right', paddingBottom: '24px'}}>
					<Button type='primary' onClick={this.openModal}></Button>
				</div>
				<Table columns={this.columns}/>
				<AddModalWithForm visible={visible}/>
			</BasicLayout>
		)
	}
	openModal = () => {
		store.visible = true;
	};
	columns = [{
		title: '',
		key: 'type',
		dataIndex: 'type'
	}, {
		title: '',
		dataIndex: 'content'
	}, {
		title: '',
		key: 'operate',
		render: (text, record) => (
			<div>
				<a></a>
			</div>
		)
	}]
}

export default ServiceConfig;